* {
  box-sizing: border-box;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  background-color: #fafafa;
  margin: 0;
  font-family: Oswald, Cinzel, -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen-Sans, Ubuntu, Cantarell, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

img {
  max-width: 100%;
  display: block;
}

ul, a, p {
  margin: 0;
  padding: 0;
  text-decoration: none;
  list-style: none;
}

.section {
  padding: 20px 0;
}

@media (min-width: 768px) {
  .section {
    padding: 40px 0;
  }
}

.container {
  width: calc(100% - 30px);
  margin: 0 auto;
}

@media (min-width: 768px) {
  .container {
    width: calc(100% - 100px);
  }
}

